import React, { Component } from 'react';
import moment from 'moment';
// require('../assets/js/scripts');

class Sort extends Component {
  sortResources(type) {
    const { resources, handleResourceChange } = this.props;
    switch(type) {
      case 'date': {
        const sorted = Object.values(resources).sort((a,b) =>
          moment(a.date_created, "DD-MM-YYYY").format() > moment(b.date_created, "DD-MM-YYYY").format());
        handleResourceChange(sorted);
      }
      break;
    }
  }
  filterResources(type) {
    const { resources, handleResourceChange } = this.props;
    console.log(type);
    function createFilter(filterConditionType) {
      const filtered = Object.values(resources).filter((resource) => resource.category === filterConditionType);
      handleResourceChange(filtered);
    }

    switch(type) {
      case 'landing': 
        var filterConditionType = 'landing';
        createFilter(filterConditionType);
        console.log(type);
      break;
      case 'apps':
        var filterConditionType = "app";
        createFilter(filterConditionType);
        console.log(type);
      break;
      case 'kits':
        var filterConditionType = "kit";
        createFilter(filterConditionType);
        console.log(type);
      break;
      case 'misc':
        var filterConditionType = "misc";
        createFilter(filterConditionType);
        console.log(type);
    }
  }

  render() {
    const { handleResourceChange, resources } = this.props;
    return (
        <div className="row middle-xs">
          <div className="sort-row">
            <div className="sort">
              <ul className="filters">
                <li><button className="filter active" data-filter="ping" data-reset="true" onClick={() => handleResourceChange(resources)} >All</button></li>
                <li><button className="filter" data-filter="ping" data-filter-tag="app" onClick={() => this.filterResources('landing')} >Landing Page</button></li>
                <li><button className="filter" data-filter="ping" data-filter-tag="app" onClick={() => this.filterResources('apps')} >Apps</button></li>
                <li><button className="filter" data-filter="ping" data-filter-tag="app" onClick={() => this.filterResources('kits')} >UI Kits</button></li>
                <li><button className="filter" data-filter="ping" data-filter-tag="app" onClick={() => this.filterResources('misc')} >Misc</button></li>
              </ul>
              <div onClick={() => this.sortResources('date')} className="sort-action">
                Sort by : Date Added
              </div>
            </div>
          </div>
        </div>
    );
  }
}

export default Sort;


