import React, { Component } from 'react';

class Hero extends Component {
  render() {
    return (
      <div className="hero" id="hero">
        <div className="row middle-xs hero-content">
          <div className="hero">
            <div className="col-xs-5">
              <div className="about">A collection of design resources for the figma community.</div>
              <a href="#" className="col-xs-8 cta">Subscribe to our newsletter.</a>
            </div>
            <div className="col-xs-7 signup">
              <div id="mc_embed_signup">
              <form action="https://figmafreebi.us17.list-manage.com/subscribe/post?u=9cb9040f9f874a84458ccbbc5&amp;id=783b92c662" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_blank" noValidate>
                  <div id="mc_embed_signup_scroll" className="signup-form">
                    <input type="email" value={this.props.email} name="EMAIL" className="email" id="mce-EMAIL" placeholder="Email Address" required />
                    <div className="secure" aria-hidden="true"><input type="text" name="b_9cb9040f9f874a84458ccbbc5_783b92c662" tabIndex="-1" value="" /></div>
                    <div className="clear"><input type="submit" value="Get Freebies to your Mail" name="subscribe" id="mc-embedded-subscribe" className="button btn-sign" /></div>
                  </div>
              </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Hero;


