import React, { Component } from 'react';
import Header from '../header';
import fire from '../fire';
import '../assets/css/admin.css';

class Admin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      category: '',
      thumb: '',
      link: '',
      author: '',
      authorLink: '',
      avatar: '',
      created: '',
      type: '',
      originalLink: '',
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.value;
    this.setState({
      [[name]]: value
    });
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {
    alert('A name was submitted: ' + this.state.name + ' ' + this.state.author );
    event.preventDefault();
  }

  addResource(event) {
    event.preventDefault();
    console.log('Resource Submitted');

    /* Send the resource to Firebase */

    const resourceListRef = fire.database().ref('resources');
    const newResourceRef = resourceListRef.push();

    newResourceRef.set({
      'author': this.state.author,
      'authorLink': this.state.authorLink,
      'category': this.state.category,
      'created': this.state.created,
      'avatar': this.state.avatar,
      'link': this.state.link,
      'name': this.state.name,
      'thumb': this.state.thumb,
      'type': this.state.type,
      'originalLink': this.state.originalLink
    });

    alert(this.state.name + ' was submitted!'  );


  }

  render() {
    const { resources } = this.props;
    return (
      <div className="admin" id="admin">
        <Header />
        <div className="admin-form">
            <form className="form" onSubmit={this.addResource.bind(this)}>
              <div className="freebie">
                <label>
                  Freebie Name:
                  <input type="text" name="name" placeholder={'Freebie Name'} value={this.state.name} onChange={this.handleInputChange} />
                </label>
                <label>
                  Freebie Category:
                  <input type="text" name="category" placeholder={'Enter Category'} value={this.state.category} onChange={this.handleInputChange} />
                </label>
                <label>
                  Freebie Image:
                  <input type="text" name="thumb" placeholder={'Thumbnail'} value={this.state.thumb} onChange={this.handleInputChange} />
                </label>
                <label>
                  Freebie Link:
                  <input type="text" name="link" placeholder={'Link to Product'} value={this.state.link} onChange={this.handleInputChange} />
                </label>
                <label>
                  Type:
                  <input type="text" name="type" placeholder={'Type'} value={this.state.type} onChange={this.handleInputChange} />
                </label>
                <label>
                  Original link:
                  <input type="text" name="originalLink" placeholder={'Original Link'} value={this.state.originalLink} onChange={this.handleInputChange} />
                </label>
              </div>
              <div className="author">
                <label>
                  Author Name:
                  <input type="text" name="author" placeholder={'Author Name'} value={this.state.author} onChange={this.handleInputChange} />
                </label>
                <label>
                  Author Link:
                  <input type="text" name="authorLink" placeholder={'Author Link'} value={this.state.authorLink} onChange={this.handleInputChange} />
                </label>
                <label>
                  Avatar:
                  <input type="text" name="avatar" placeholder={'Avatar'} value={this.state.avatar} onChange={this.handleInputChange} />
                </label>
                <label>
                  Created Date:
                  <input type="text" name="created" placeholder={'Created On'} value={this.state.created} onChange={this.handleInputChange} />
                </label>
                <input type="submit" value="Submit" />
              </div>
            </form>
        </div>
        
      </div>
    );
  }
}
export default Admin;


