import React, { Component } from 'react';
import Hero from '../components/hero';
import Header from '../header';
import '../assets/css/about.css';

class About extends Component {
  render() {
    return (
      <div className="about" id="about">
        <Header />
        <div className="about-wrapper">
            <p className="about-intro">FigmaFreebies is designed and build by  
            <a href="http://www.kshitij.ws" target="_blank" norefer> Kshitij Choudhary </a></p>
            <p className="about-intro">You can reach out to him via his website above or just mail him hello@kshitij.ws </p>
            <p>FigmaFreebies is just a platform of giving back to the figma community and the awesome people who are making Figma. 
            Figmafreebies is a collection of freebies that can help you kickstart your next project.</p>
            <p>Most of the freebies are indexed from all over the internet and there are 3 freebies added each week by Kshitij.</p>
        </div>
      </div>
    );
  }
}
export default About;


