import React, { Component } from 'react';
import arrow from '../assets/link-arrow.png';
import arrowWhite from '../assets/link-arrow-w.png';
import loadingImage from '../assets/loading.gif';

// import './assets/js/scripts.js';

class Card extends Component {
  state = {
    src: arrow,
  }

  handleHover(event) {
    this.setState({src: arrowWhite});
  }


  handleNormal(event) {
    this.setState({src: arrow});
  }
 
  render() {
    const { resource , key } = this.props;
    return (
        <li key={key} className="col-xs-12 col-sm-12 col-md-4 cards">
          <figure className="card">
            <div style={{ color: 'blue', height: '300px', width: '100%', backgroundSize:'cover', backgroundPosition: 'center', backgroundImage: 'url(' + resource.thumb + '), url(' + loadingImage + ') '}}></div>
            <figcaption className="card__details">
              <div className="card__description">
                <h3 className="card__heading">{resource.name}</h3>                  
                <div className="card__labels"><a href={resource.authorLink} target="_blank" norefer><img src={resource.avatar} alt={resource.author}/>{resource.author}</a></div>                    
              </div>
              <div className="card__actions" onMouseOver={this.handleHover.bind(this)} onMouseOut={this.handleNormal.bind(this)}>
                <a href={resource.link} className="primary-link"><div className="link-text">Resource Link</div><img src={this.state.src} alt=""/></a>
              </div>
            </figcaption>
          </figure>
        </li>
    );
  }
}
export default Card;


