import React, { Component } from 'react';
import fire from './fire';
import Header from './header';
import Footer from './components/footer';
import Cards from './cards';
import Hero from './components/hero';
import Sort from './components/sort';
import Pagination from "react-js-pagination";
import './App.css'; // Tell Webpack that App.js uses these styles

class App extends Component {
  constructor(props) {
    super(props);
        // let database = fire.database();

    // this.database = fire.database().ref().child('recommendation');
    this.database = fire.database().ref().child('resources');

    this.state = { 
      resources: {},
      allResources: {},
      activePage: 1
    }; // <- set up react state
  }

  componentDidMount(){
    this.database.on('value', snap => {
      // console.log(snap.val());
      this.setState({
        // recommendation : snap.val()
           // id: snap.val()
           resources: snap.val(),
           allResources: snap.val()
      })
    })
  }

  handleResourceChange(resources) {
    this.setState({ resources });
  }

  componentWillMount(){
    /* Create reference to messages in Firebase Database */
    let messagesRef = fire.database().ref('messages').orderByKey().limitToLast(100);
    messagesRef.on('child_added', snapshot => {
      /* Update React state when message is added at Firebase Database */
      let message = { text: snapshot.val(), id: snapshot.key };
      this.setState({ messages: [message].concat(this.state.messages) });
    })
  }

  addMessage(e){
    e.preventDefault();
    /* Send the message to Firebase */
    fire.database().ref('messages').push( this.inputEl.value );
    this.inputEl.value = ''; // <- clear the input
  }

  scrollToTop(scrollDuration) {
    var cosParameter = window.scrollY / 2,
        scrollCount = 0,
        oldTimestamp = performance.now();
    function step (newTimestamp) {
        scrollCount += Math.PI / (scrollDuration / (newTimestamp - oldTimestamp));
        if (scrollCount >= Math.PI) window.scrollTo(0, 0);
        if (window.scrollY === 0) return;
        window.scrollTo(0, Math.round(cosParameter + cosParameter * Math.cos(scrollCount)));
        oldTimestamp = newTimestamp;
        window.requestAnimationFrame(step);
    }
    window.requestAnimationFrame(step);
}

  handlePageChange(pageNumber) {
    this.setState({activePage: pageNumber});
    this.scrollToTop(500);

  }

  // componentWillUnmount() { 
  //   this.firebaseRef.off();
  // }

  render() {
    var mostViewedPosts = fire.database().ref('posts').orderByChild('metrics/views');
    const { resources, activePage, allResources } = this.state;

    return (
      <div className="body">
        <Header />
        <div className="main">
          <div className="container">
            <Sort handleResourceChange={this.handleResourceChange.bind(this)} resources={allResources} />
            <div className="row middle-xs" id="cards">
              <ul className="flex-list" ref='freebie'>
                <Cards resources={resources} activePage={activePage} />
              </ul>
            </div>
            <Pagination
              activePage={this.state.activePage}
              itemsCountPerPage={4}
              getPageUrl
              totalItemsCount={12}
              pageRangeDisplayed={5}
              onChange={this.handlePageChange.bind(this)}
            />
          </div>
        </div>
        <Hero />
        <Footer />
      </div>
    );
  }
}

export default App;


